import * as React from 'react';
import { css } from 'glamor';

const iconBackArrow = require('../../assets/iconBackArrowBlk.svg');
import { TEXT } from '../../styles/variables';

export interface Props {
  hideBottomBorder?: boolean;
  title: string;
  contentCenter?: any;
  contentRight?: any;
}

const navBarStyles = (hideBottomBorder: boolean) =>
  css({
    width: '100%',
    height: '100px',
    borderBottom: !hideBottomBorder && '1px solid rgb(232, 232, 232)',
    display: 'flex',
    alignItems: 'center',
    flexDirection: 'row'
  });

const titleStyles = css({
  fontSize: 10,
  fontWeight: 'bold',
  letterSpacing: 2
});

const backButton = css({
  height: 20,
  marginRight: 20,
  width: 'auto'
});

const baseContentOptions = {
  alignItems: 'center',
  display: 'flex',
  flexDirection: 'row',
  fontSize: 10,
  fontWeight: 'bold',
  textTransform: 'uppercase',
  textDecoration: 'none',
  color: TEXT.color
};

const leftContent = css(baseContentOptions, {
  flexGrow: '1',
  paddingRight: '40px',
  marginLeft: 40
});

const centerContent = css({
  flexGrow: '3'
});

const rightContent = css(baseContentOptions, {
  flexGrow: '1',
  justifyContent: 'flex-end',
  marginRight: 40
});

export const TopNav = ({
  title,
  contentCenter,
  contentRight,
  hideBottomBorder
}: Props) => (
  <div {...navBarStyles(hideBottomBorder)}>
    <a {...leftContent} href="/">
      <img {...backButton} src={iconBackArrow} />
      <div {...titleStyles}>{title}</div>
    </a>
    <div {...centerContent}>{contentCenter}</div>
    <div {...rightContent}>{contentRight}</div>
  </div>
);



